import { Fragment, useState, useEffect, useRef } from 'react'
import {
    X, Share, Printer, FileText, File, Grid, Copy, Plus, MoreVertical,
    Edit, Archive, Trash, Search, ChevronLeft, Eye, XCircle, Facebook, Globe, Instagram, Twitter
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, CardBody, CustomInput, Table, Spinner, InputGroup, InputGroupAddon, InputGroupText, FormFeedback, Progress, Modal, ModalHeader, ModalBody, ModalFooter
} from 'reactstrap'
import useJwt from '@src/auth/jwt/useJwt'
import { Error, Success, ErrorMessage } from '../../../../../viewhelper'
import { useHistory } from 'react-router-dom'
import Select from 'react-select'
import { selectThemeColors, transformInToFormObject } from '@utils'

const EditModal = ({ modal, toggleModal, serviceLogicInfo, setserviceLogicInfo, serviceInfo, setserviceInfo, setReset, resetData }) => {
    const [editPointRuleloading, seteditPointRuleloading] = useState(false)
    const [keyword, setKeyword] = useState('')

    const handleChange = (e) => {
        setserviceInfo({ ...serviceInfo, [e.target.name]: e.target.value })
    }
    const handleChange1 = (e) => {
        setserviceLogicInfo({ ...serviceLogicInfo, [e.target.name]: e.target.value })
    }

    const onSubmit = (e) => {
        e.preventDefault()
        const { id : sid, serviceId, serviceKeyword, isSubCategory, subTypes } = serviceInfo
        console.log({ id : sid, serviceId, serviceKeyword, subTypes })
        localStorage.setItem('useBMStoken', true)
        seteditPointRuleloading(true)
        useJwt.editService(serviceId, { serviceId,  serviceKeyword: serviceKeyword.toLowerCase(), isSubCategory, subTypes }).then(res => {
            console.log(res)
            // Success(res)
        }).catch(err => {
            Error(err)
            console.log(err)
        })
        const { id : slid, ruleProvider, isFinancial, minimum, maximum} = serviceLogicInfo
        useJwt.editServiceLogic(slid, {id : slid, serviceId, ruleProvider, isFinancial, minimum, maximum}).then(res => {
            setReset(!resetData)
            seteditPointRuleloading(false)
            console.log(res)
            toggleModal()
            Success(res)
        }).catch(err => {
            seteditPointRuleloading(false)
            localStorage.setItem('useBMStoken', false)
            Error(err)
            console.log(err.response)
        })
    }
    return (
        <Modal isOpen={modal} toggle={toggleModal} className='modal-dialog-centered'>
            <ModalHeader toggle={toggleModal}>Edit Service</ModalHeader>
            <ModalBody>
                <Form className="row" style={{ width: '100%' }} onSubmit={onSubmit} autoComplete="off">
                    <Col sm="6" >
                        <FormGroup>
                            <Label for="serviceId">Service ID</Label>
                            <Input type="number"
                                name="serviceId"
                                id='serviceId'
                                value={serviceInfo.serviceId}
                                onChange={handleChange}
                                disabled
                                placeholder="0"
                            />
                        </FormGroup>
                    </Col>
                    <Col sm="6" >
                        <FormGroup>
                            <Label for="serviceKeyword">Service Keyword</Label>
                            <Input type="text"
                                name="serviceKeyword"
                                id='serviceKeyword'
                                value={serviceInfo?.serviceKeyword}
                                onChange={handleChange}
                                required
                                placeholder="keyword"
                            />
                        </FormGroup>
                    </Col>
                    <Col sm="12" className='mt-1'>
                        <FormGroup>
                           <CustomInput 
                            type='switch'
                            label='Allow subtype?'
                            id = 'isSubCategory'
                            checked = {serviceInfo?.isSubCategory}
                            onChange={(e) => {
                                if (e.target.checked) { 
                                    setserviceInfo({ ...serviceInfo, isSubCategory: true })
                                } else {
                                    setserviceInfo({ ...serviceInfo, isSubCategory: false, subTypes: [] })
                                }
                            }
                         }
                           />
                        </FormGroup>
                    </Col>
                    {
                        serviceInfo.isSubCategory && <Col sm="12" >
                        <FormGroup>
                            <Label for="keyword">Subtypes</Label>
                            <div className='d-flex align-items-center'>
                                <InputGroup>
                                    <Input type="text"
                                        name="keyword"
                                        id='keyword'
                                        value={keyword}
                                        onChange={e => setKeyword(e.target.value)}
                                        placeholder="your answer"
                                    />
                                    <InputGroupAddon addonType='append'>
                                        <Button style={{zIndex:'0'}} color='primary' outline onClick={() => {
                                            if (keyword) {
                                                setserviceInfo({ ...serviceInfo, subTypes: [...serviceInfo.subTypes, keyword] }) 
                                                setKeyword('')
                                            }
                                        }}>
                                            Add
                                        </Button>
                                    </InputGroupAddon>
                                </InputGroup>
                            </div>
                            <div className='d-flex mt-1'>
                                {serviceInfo.subTypes?.map((k, index) =>  <InputGroup key={index} style={{width:'100px', marginRight:'10px'}}>
                                    <InputGroupAddon addonType='prepend'>
                                        <Button style={{width:'35px', padding:'5px'}} color='primary' outline onClick={() => {
                                            serviceInfo.subTypes.splice(serviceInfo.subTypes.indexOf(k), 1)
                                            setserviceInfo({ ...serviceInfo, subTypes: [...serviceInfo.subTypes] })
                                        }}>
                                            <X size={12} />
                                        </Button>
                                    </InputGroupAddon>
                                    <Input type="text"
                                        name="keyword"
                                        id='keyword'
                                        style={{fontSize:'10px', padding:'5px'}}
                                        value={k}
                                        disabled
                                        onChange={() => {}}
                                    />
                                </InputGroup>)}
                            </div>
                        </FormGroup>
                    </Col>
                    }
                    <Col sm='12'>
                        <p>Service Logic Info</p>
                    </Col>
                    <Col sm="6" >
                        <FormGroup>
                            <Label for="minimum">MIN TXN Amount</Label>
                            <Input type="number"
                                name="minimum"
                                id='minimum'
                                value={serviceLogicInfo?.minimum}
                                onChange={handleChange1}
                                required
                                placeholder= '0'
                            />
                        </FormGroup>
                    </Col>
                    <Col sm="6" >
                        <FormGroup>
                            <Label for="maximum">MAX TXN Amount</Label>
                            <Input type="number"
                                name="maximum"
                                id='maximum'
                                value={serviceLogicInfo?.maximum}
                                onChange={handleChange1}
                                required
                                placeholder="0"
                            />
                        </FormGroup>
                    </Col>
                    <Col sm="6" >
                        <FormGroup>
                            <Label for="senGroupType">Sender Group Type</Label>
                            <Input type="text"
                                name="senGroupType"
                                id='senGroupType'
                                value={[{ value: 1, label: 'Customer' }, { value: 2, label: 'Agent' }, { value: 3, label: 'Merchant' }, { value: 0, label: 'Any' }].find(item => item.value === serviceLogicInfo?.senGroupType)?.label}
                                onChange={handleChange1}
                                disabled
                            />
                        </FormGroup>
                    </Col>
                    <Col sm="6" >
                        <FormGroup>
                            <Label for="recGroupType">Receiver Group Type</Label>
                            <Input type="text"
                                name="recGroupType"
                                id='recGroupType'
                                value={[{ value: 1, label: 'Customer' }, { value: 2, label: 'Agent' }, { value: 3, label: 'Merchant' }, { value: 0, label: 'Any' }].find(item => item.value === serviceLogicInfo?.recGroupType)?.label}
                                onChange={handleChange1}
                                disabled
                            />
                        </FormGroup>
                    </Col>
                    <Col sm="6" >
                        <FormGroup>
                            <Label for="ruleProvider">Reward Priority</Label>
                            <Input type="text"
                                name="ruleProvider"
                                id='ruleProvider'
                                value={serviceLogicInfo?.ruleProvider === 's' ? 'Sender' : serviceLogicInfo?.ruleProvider === 'r' ? 'Reciever' : ''}
                                onChange={handleChange1}
                                disabled
                            />
                        </FormGroup>
                    </Col>
                    <Col sm="12" >
                            <FormGroup check>
                                <Input onChange={(e) => {
                                        if (e.target.checked) { 
                                            setserviceLogicInfo({ ...serviceLogicInfo, isFinancial: true })
                                        } else {
                                            setserviceLogicInfo({ ...serviceLogicInfo, isFinancial: false })
                                        }
                                    }
                                } type='checkbox' id='isFinancial' checked = {serviceLogicInfo?.isFinancial}/>
                                <Label for='isFinancial' check>
                                Is Financial?
                                </Label>
                            </FormGroup>
                        </Col>
                    <Col sm="12" className='text-center'>
                        {
                            editPointRuleloading ? <Button.Ripple color='primary' className='mr-1' disabled style={{ marginTop: '25px' }}>
                                <Spinner color='white' size='sm' />
                                <span className='ml-50'>Loading...</span>
                            </Button.Ripple> : <Button.Ripple className='ml-2' color='primary' type="submit" style={{ marginTop: '25px' }}>
                                <span >Update</span>
                            </Button.Ripple>
                        }
                    </Col>
                </Form>
            </ModalBody>
        </Modal>
    )
}
export default EditModal